import React from 'react'
import PropTypes from 'prop-types'
import { TweenMax, Power4, Expo } from 'gsap'
// import { Link, Router } from '../../routes'
import { Link } from '../../routes'
import ActiveLink from './activeLink'
import styled from 'styled-components'
import { withI18next } from '../lib/withI18next'
// import { Button } from 'reactstrap'

import icoHome from '../static/ico-home.png'
import icoAbout from '../static/ico-about.png'
import icoWork from '../static/ico-work.png'
import icoContact from '../static/ico-contact.png'
// import icoFullscreen from '../static/ico-fullscreen.png'

class Nav extends React.Component {
  static propTypes = {
    t: PropTypes.any,
    i18n: PropTypes.object
  }

  constructor (props) {
    super(props)

    this.menuBg = React.createRef()
    this.menuContent = React.createRef()

    this.toggleMenu = this.toggleMenu.bind(this)

    this.state = {
      isOpen: false
    }
  }

  toggleMenu (e) {
    if (!this.state.isOpen) {
      this.setState({ isOpen: true })
      TweenMax.set('html,body', { overflow:'hidden' })
      let windowSize = window.innerWidth > window.innerHeight ? window.innerWidth : window.innerHeight
      let radio = 1.4
      TweenMax.to(this.menuBg.current.childNodes[0], 0.6, { width:windowSize * radio, height:windowSize * radio, ease:Expo.out })
      TweenMax.to(this.menuBg.current.childNodes[0].childNodes[0], 0.6, { width:windowSize * radio, height:windowSize * radio, delay:0.15, ease:Expo.out })
      TweenMax.to(this.menuBg.current.childNodes[0].childNodes[0].childNodes[0], 0.6, { width:windowSize * radio, height:windowSize * radio, delay:0.3, ease:Expo.out })

      let menuContentItem = document.querySelectorAll('.menuContent a')
      TweenMax.set(this.menuContent.current, { display:'block' })
      TweenMax.set(menuContentItem, { y:-10, autoAlpha:0 })
      TweenMax.to(this.menuContent.current, 0.7, { autoAlpha:1, delay:0.3, ease:Power4.out })
      TweenMax.staggerTo(menuContentItem, 0.7, { y:0, autoAlpha:1, delay:0.3, ease:Power4.out }, 0.1)
    } else {
      this.setState({ isOpen: false })
      TweenMax.set('html,body', { overflow:'auto' })
      TweenMax.to(this.menuContent.current, 0.2, { display:'none', autoAlpha:0 })
      TweenMax.to(this.menuBg.current.childNodes[0], 0.4, { width:0, height:0, delay:0.1, ease:Expo.out })
      TweenMax.to(this.menuBg.current.childNodes[0].childNodes[0], 0.4, { width:0, height:0, delay:0.1, ease:Expo.out })
      TweenMax.to(this.menuBg.current.childNodes[0].childNodes[0].childNodes[0], 0.4, { width:0, height:0, delay:0.1, ease:Expo.out })
    }
  }

  componentDidMount () {
    window.addEventListener('resize', () => {
      if (window.innerWidth > 768) {
        this.setState({ isOpen: false })
        TweenMax.set('html,body', { overflow:'auto' })
        TweenMax.set(this.menuBg.current.childNodes[0], { width:0, height:0 })
        TweenMax.set(this.menuBg.current.childNodes[0].childNodes[0], { width:0, height:0 })
        TweenMax.set(this.menuBg.current.childNodes[0].childNodes[0].childNodes[0], { width:0, height:0 })
        TweenMax.set(this.menuContent.current, { display:'block', autoAlpha:1 })
      } else {
        if (!this.state.isOpen) {
          TweenMax.set('html,body', { overflow:'auto' })
          TweenMax.set(this.menuContent.current, { display:'none', autoAlpha:0 })
        }
      }
    })

    let menuContentItem = document.querySelectorAll('.menuContent a')
    for (let i = 0; i < menuContentItem.length; i++) {
      menuContentItem[i].addEventListener('click', () => {
        if (document.querySelectorAll('body .mobile').length) {
          this.setState({ isOpen: false })
          setTimeout(() => {
            TweenMax.set('html,body', { overflow:'auto' })
            TweenMax.set(this.menuBg.current.childNodes[0], { width:0, height:0 })
            TweenMax.set(this.menuBg.current.childNodes[0].childNodes[0], { width:0, height:0 })
            TweenMax.set(this.menuBg.current.childNodes[0].childNodes[0].childNodes[0], { width:0, height:0 })
            TweenMax.set(this.menuContent.current, { display:'none', autoAlpha:0 })
          }, 500)
        }
      }, false)
    }
  }

  render () {
    const { t, i18n } = this.props

    return (
      <Navigation>
        <div className='menuContainer font-muli px-0 px-md-4'>
          <div className='burger' onClick={this.toggleMenu}>
            <span />
            <span />
            <span />
          </div>
          <div className='menuBg' ref={this.menuBg}><div><div><div /></div></div></div>
          <div className='menuContent' ref={this.menuContent}>
            {i18n.language
              ? <div>
                <ActiveLink prefetch href='/'>
                  <span><img src={icoHome} />{t('nav_home')}</span>
                </ActiveLink>
                <span>{' / '}</span>
                <ActiveLink prefetch href='about' params={{ lng: i18n.language }}>
                  <span><img src={icoAbout} />{t('nav_about')}</span>
                </ActiveLink>
                <span>{' / '}</span>
                <ActiveLink prefetch href='blog' params={{ slug: 'hello-world' }}>
                  <span><img src={icoWork} />{t('nav_works')}</span>
                </ActiveLink>
                {/* <Link prefetch href='/'>
                  <a><span><img src={icoHome} />{t('nav_home')}</span></a>
                </Link>
                <span>{' / '}</span>
                <Link prefetch route='about' params={{ lng: i18n.language }}>
                  <a><span><img src={icoAbout} />{t('nav_about')}</span></a>
                </Link>
                <span>{' / '}</span>
                <Link prefetch route='blog' params={{ slug: 'hello-world' }}>
                  <a><span><img src={icoWork} />{t('nav_works')}</span></a>
                </Link> */}
                <span>{' / '}</span>
                <a href='javascript:void(0);'><span><img src={icoContact} />{t('nav_contact')}</span></a>
              </div> : ''}
          </div>
          <div className='closeBtn' onClick={this.toggleMenu}>close</div>
          <div className='copyright'>{t('nav_copyright')}</div>
        </div>
      </Navigation>
    )
  }
}

const Navigation = styled.nav`
  z-index:1;

  .menuContainer {
    position:fixed;
    bottom:0; left:0;
    width:100%;
    min-height:46px;
    background-color:rgba(0, 0, 0, 0.5);
    z-index:1;
    font-size:12px;

    .burger {
      display:none;
      position:absolute;
      top:0; left:0;
      width:70px; height:60px;
      padding:10px 15px;
      z-index:1;

      span {
        display: block;
        width:100%; height:4px;
        background-color:rgba(255, 255, 255, 1);
        border-radius:3px;
        margin-top:7px;
      }
    }

    .closeBtn {
      display:none;
      position:absolute;
      bottom:0; right:0;
      color:#000;
      z-index:1;
    }

    .menuBg {
      position:absolute;
      top:0; left:0;
      width:100%; height:100%;
      overflow:hidden;
      div {
        position:absolute;
        top:0; left:0;
        /* background-color:#1a1d24; */
        background-color:#e45fc0;
        border-bottom-right-radius:100%;
        z-index:1;

        div {
          position:absolute;
          top:0; left:0;
          background-color:#5fe4e2;
          border-bottom-right-radius:100%;

          div {
            position:absolute;
            top:0; left:0;
            background-color:#fff;
            border-bottom-right-radius:100%;
          }
        }
      }
    }

    .menuContent {
      position:relative;
      float:right;
      color:#9d9d9d;
      z-index:1;

      a {
        position:relative;
        display:inline-block;
        color:#9d9d9d;
        text-decoration:none;
        padding:14px 20px;

        span {
          position:relative;
          display:inline-block;
          padding:0 0 0 20px;
          transition:0.5s;

          img {
            opacity:0.5;
            position: absolute;
            top: 50%; left: 0;
            -webkit-transform: translate(0, -50%);
            -moz-transform: translate(0, -50%);
            -o-transform: translate(0, -50%);
            -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
            transition:0.5s;
          }
        }
      }
    }

    .copyright {
      float:left;
      color:#fff;
      padding:14px 0;
    }
  }
  @media (min-width: 769px) {
    .menuContainer {
      .menuContent {
        a {
          &:hover {
            color:#fff;
            span {
              img {
                opacity:1;
              }
            }
          }

          &.active-link {
            color:#fff;
            font-weight: bold;

            &:before {
              content:'';
              position:absolute;
              top:0; left:20px;
              width:calc(100% - 40px);
              border-top:3px solid #5fe4e2;
            }

            img {
              opacity:1;
            }
          }
        }
      }
    }
  }

  @media (max-width: 768px) {
    .menuContainer {
      position:static;
      text-align:center;
      top:0; bottom:inherit;
      font-size:30px;
      background:none;
      /* height: 100%; */
      min-height: auto;

      .burger {
        display:block;
      }

      .closeBtn {
        display:block;
      }

      .menuContent {
        display:none;
        float:none;
        opacity:0;
        position:absolute;
        top:50%; left:0;
        width:100%;
        transform: translate(0, -50%);
        a {
          display:block;
          padding-top:30px;
          padding-bottom:30px;
          &:active {
            color:#1a1d24;
          }
          span {
            transition:0.3s;
            img {
              display:none;
            }
          }
        }
        span {
          display:none;
        }
      }

      .copyright {
        display:none;
        float:none;
        padding-top:0;
      }
    }
  }
`

export default withI18next(['common'])(Nav)
